import React, { useState, FormEvent } from 'react';

import {
  Paper,
  Container,
  TextField,
  Typography,
  Button,
  Box,
  Chip,
  Grid,
} from '@mui/material';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import PlaylistAddIcon from '@mui/icons-material/PlaylistAdd';

import SelectBox from '../../components/common/SelectBox';
import Editor from '../../components/common/Editor';

const Purpose = [
  { value: '', label: ' ' },
  { value: '', label: '' },
  { value: '', label: ' ' },
  { value: '', label: ' ' },
];
const Level = [
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '', label: '' },
];
const Target = [
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '', label: '' },
];

const NewRoutine = () => {
  const [title, setTitle] = useState('');
  const [purpose, setPurpose] = React.useState<string | null>('');
  const [level, setLevel] = React.useState<string | null>('');
  const [target, setTarget] = React.useState<string | null>('');
  const [content, setContent] = useState('');

  const [routineItem, setRoutineItem] = useState(''); //    
  const [lists, setLists] = useState<string[]>([]); //   

  //  /    
  const [isPublic, setIsPublic] = useState(true);

  //    
  const handleToggle = (event: React.ChangeEvent<HTMLInputElement>) => {
    setIsPublic(event.target.checked);
    //      . :   
  };

  //    
  const handleAddRoutineItem = () => {
    if (routineItem.trim()) {
      setLists([...lists, routineItem.trim()]);
      setRoutineItem('');
    }
  };

  //   
  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    console.log(':', isPublic);
    console.log(':', title);
    console.log(':', purpose);
    console.log(':', level);
    console.log(':', target);
    console.log(' :', lists);
    console.log(':', content);
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Paper sx={{ p: 3, margin: 'auto' }}>
        <form onSubmit={handleSubmit}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <Typography variant="h6" component="h2" sx={{ mb: 2 }}>
                
            </Typography>
            <FormControlLabel
              control={<Switch checked={isPublic} onChange={handleToggle} />}
              label={isPublic ? 'Public' : 'Private'}
              required
            />
          </Box>
          <TextField
            label=""
            fullWidth
            variant="outlined"
            sx={{ mb: 2 }}
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
          <Box sx={{ display: 'flex' }}>
            <SelectBox
              label=" "
              options={Purpose}
              helperText="   ."
              onChange={setPurpose}
            />
            <SelectBox
              label=""
              options={Level}
              helperText=" ."
              onChange={setLevel}
            />
            <SelectBox
              label=" "
              allowNull
              options={Target}
              helperText="  ."
              onChange={setTarget}
            />
          </Box>

          <Grid container spacing={1} alignItems="center">
            <Grid item xs={11}>
              <TextField
                label="  "
                fullWidth
                variant="outlined"
                value={routineItem}
                onChange={(e) => setRoutineItem(e.target.value)}
                onKeyPress={(event) => {
                  if (event.key === 'Enter') {
                    event.preventDefault();
                    handleAddRoutineItem();
                  }
                }}
              />
            </Grid>
            <Grid item xs={1}>
              <Button
                onClick={handleAddRoutineItem}
                variant="contained"
                color="primary"
                sx={{ height: '54px' }}
              >
                <PlaylistAddIcon />
              </Button>
            </Grid>
          </Grid>
          <Box
            sx={{
              borderColor: 'primary.main',
              display: 'flex',
              flexDirection: 'column',
              flexWrap: 'wrap',
              gap: 1,
              mt: 0,
              mb: 1,
              minHeight: '50px',
              alignItems: 'flex-start',
              padding: '8px',
            }}
          >
            {lists.map((item, index) => (
              <Chip
                color="primary"
                variant="outlined"
                label={item}
                key={index}
                onDelete={() => {
                  setLists(lists.filter((_, idx) => idx !== index));
                }}
              />
            ))}
          </Box>

          <Box sx={{ mb: 2, minHeight: 500 }}>
            <Editor
              placeholder="  ."
              value={content}
              onChange={setContent}
            />
          </Box>
          <Box display="flex" justifyContent="flex-end">
            <Button type="submit" variant="contained" color="primary">
               
            </Button>
          </Box>
        </form>
      </Paper>
    </Container>
  );
};

export default NewRoutine;
