import * as React from 'react';

import { Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';

import SearchBox from '../common/search/SearchBox';
import GenericTable from '../genericTable/GenericTable';
import {
  TableData,
  Table,
  TableHead,
  TableRow,
} from '../genericTable/GenericTable.styles';
import ButtonNewPost from '../common/button/ButtonNewPost';

interface BoardProps {
  selectedCategory: string | number | null;
}

const Board: React.FC<BoardProps> = ({ selectedCategory }) => {
  const [searchTerm, setSearchTerm] = React.useState('');
  const navigate = useNavigate(); // useNavigate  

  //    ( + )
  const filteredPosts = React.useMemo(() => {
    let filtered = examplePosts;

    //  
    if (selectedCategory === '') {
      //       
      filtered = filtered
        .filter((post) =>
          post.title.toLowerCase().includes(searchTerm.toLowerCase()),
        )
        .sort((a, b) => {
          // 1:    
          if (b.like !== a.like) {
            return b.like - a.like;
          }
          // 2: /    ( 0  1   )
          const ratioA = a.like / (a.view || 1);
          const ratioB = b.like / (b.view || 1);
          if (ratioB !== ratioA) {
            return ratioB - ratioA;
          }
          // 3:    (    )
          return b.created_at.getTime() - a.created_at.getTime();
        });
    } else if (selectedCategory) {
      //         
      filtered = filtered.filter(
        (post) =>
          post.category === selectedCategory &&
          post.title.toLowerCase().includes(searchTerm.toLowerCase()),
      );
    } else {
      //      
      filtered = filtered.filter((post) =>
        post.title.toLowerCase().includes(searchTerm.toLowerCase()),
      );
    }

    return filtered;
  }, [selectedCategory, searchTerm]);

  const handleRowClick = (id: string, isSelected: boolean) => {
    const post = examplePosts.find((p) => p.id === id);
    if (post) navigate(`/post/${post.no}`, { state: { post } });
  };
  const columns = [
    { id: 'title', label: 'Title', width: 200 },
    { id: 'category', label: 'Category', width: 50 },
    { id: 'author', label: 'Author', width: 40 },
    { id: 'like', label: 'Likes', width: 30 },
    { id: 'view', label: 'Views', width: 30 },
  ];

  const renderRow = (
    post: Post,
    isSelected: boolean,
    onSelect: (userId: string, isSelected: boolean) => void,
  ) => (
    <TableRow key={post.no} onClick={() => onSelect(post.id, isSelected)}>
      <TableData
        style={{
          paddingLeft: '20px',
          paddingRight: '20px',
          textAlign: 'left',
          width: 200,
        }}
      >
        {post.title}
      </TableData>
      <TableData style={{ textAlign: 'center', width: 50 }}>
        {post.category}
      </TableData>
      <TableData style={{ textAlign: 'center', width: 40 }}>
        {post.id}
      </TableData>
      <TableData style={{ textAlign: 'center', width: 30 }}>
        {post.like}
      </TableData>
      <TableData style={{ textAlign: 'center', width: 30 }}>
        {post.view}
      </TableData>
    </TableRow>
  );

  return (
    <Box sx={{ padding: 2 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', p: 1 }}>
        <Box sx={{}}>
          <ButtonNewPost />
        </Box>
        <Box sx={{ width: '40%' }}>
          <SearchBox onSearch={setSearchTerm} />
        </Box>
      </Box>
      <GenericTable<Post>
        data={examplePosts}
        columns={columns}
        rowsPerPage={20}
        renderRow={(item, isSelected) =>
          renderRow(item, isSelected, handleRowClick)
        }
      />
    </Box>
  );
};

export default Board;

type Post = {
  no: number;
  title: string;
  id: string;
  content: string;
  category: string;
  writtenTime: string;
  like: number;
  view: number;
  created_at: Date;
  updated_at: Date;
  comments: Comment[];
};

function createPostData(
  no: number,
  title: string,
  id: string,
  content: string,
  category: string,
  writtenTime: string,
  like: number,
  view: number,
  comments: Comment[],
): Post {
  return {
    no,
    title,
    id,
    content,
    category,
    writtenTime,
    like,
    view,
    created_at: new Date(),
    updated_at: new Date(),
    comments,
  };
}

