import React, { useState } from 'react';
import Box from '@mui/system/Box';
import Grid from '@mui/system/Unstable_Grid';
import { styled } from '@mui/system';
import { Typography } from '@mui/material';

// 
type Category = {
  no: number; // 
  name: string;
  cnt: number; //   
};
function createCategoryData(no: number, name: string, cnt: number): Category {
  return {
    no,
    name,
    cnt,
  };
}
//  
const exampleCategory: Category[] = [
  createCategoryData(1, '', 100),
  createCategoryData(2, '', 100),
  createCategoryData(3, '', 100),
  createCategoryData(4, '', 100),
  createCategoryData(5, '', 100),
  createCategoryData(6, '', 100),
  createCategoryData(7, '', 100),
  createCategoryData(8, '', 100),
  createCategoryData(9, '', 100),
  createCategoryData(10, '', 100),
  createCategoryData(11, '', 100),
  createCategoryData(12, '', 100),
  createCategoryData(13, '', 100),
  createCategoryData(14, '', 100),
];

//  
const Item = styled('div')(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  '&:hover': {
    backgroundColor: '#f5f5f5',
  },
  border: '1px solid',
  borderColor: theme.palette.mode === 'dark' ? '#444d58' : '#ced7e0',
  padding: theme.spacing(1),
  borderRadius: '4px',
  textAlign: 'center',
  cursor: 'pointer',
}));

type ButtonCategoryTagProps = {
  onCategorySelect: (category: string) => void;
};

const ButtonCategoryTag: React.FC<ButtonCategoryTagProps> = ({
  onCategorySelect,
}) => {
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);

  const handleCategoryClick = (category: string) => {
    setSelectedCategory(category); //    
    onCategorySelect(category); //     
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <Typography variant="h5"></Typography>
      <Grid
        container
        spacing={{ xs: 2, md: 3 }}
        columns={{ xs: 4, sm: 8, md: 12 }}
      >
        {exampleCategory.map((category) => (
          <Grid xs={2} sm={2} key={category.no}>
            <Item onClick={() => handleCategoryClick(category.name)}>
              #{category.name}
              <br />({category.cnt})
            </Item>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default ButtonCategoryTag;
